
.app-container{padding:8px 0;width:100%;display:-webkit-flex;display:-ms-flexbox;display:flex;padding-left:190px;padding-top:70px;padding-right:10px
}
.app-container .navbar-left{overflow:auto;margin-right:4px;width:180px;border-right:1px solid #efefef;box-shadow:1px 0 4px 0 rgba(18,18,18,0.1);padding-top:60px;position:fixed;height:100%;left:0;top:0;background:#2d3035
}
.app-container .router-view-box{overflow:auto;-webkit-flex:1;-ms-flex:1;flex:1
}
.app-container .router-view{padding:12px 8px;width:100%;background:#fff;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-backface-visibility:hidden;backface-visibility:hidden;box-shadow:0 1px 4px 0 rgba(18,18,18,0.1)
}


