
.login-wrap{position:fixed;z-index:0;top:0;left:0;overflow:hidden;width:100%;height:100%;background:#111
}
.login-wrap canvas{width:100%;height:100%;position:absolute
}
.login-form{position:absolute;z-index:1;top:0;bottom:0;left:0;right:0;margin:auto;padding:16px 20px 0;width:360px;height:252px;font-size:14px;background:#fff;border:1px solid #ccc;border-radius:12px;box-shadow:2px 2px 32px 1px rgba(0,0,0,0.45);opacity:.75
}
.login-form h3{margin-top:0;margin-bottom:0;padding:12px 0;font-weight:normal;font-size:22px;text-align:center
}
.login-form .field{display:block;margin:0 auto;padding:6px 0
}
.login-form .submit{padding:12px 0
}


