
.app-header{height:60px;line-height:60px;background:#fff;color:#c0ccda;display:-webkit-flex;display:-ms-flexbox;display:flex;position:fixed;width:100%;z-index:3;box-shadow:0 0 5px #f9f9f9
}
.app-header .fixed{position:absolute;top:0;bottom:0;width:100%
}
.app-header .logo-field{-webkit-flex:1;-ms-flex:1;flex:1;font-size:26px
}
.app-header .logo{width:40px;float:left;margin:10px 10px 10px 18px
}
.app-header .logout-field{width:40px;padding-right:20px;padding-top:5px
}
.app-header .logout-field i{cursor:pointer
}
.app-header .username{font-size:18px
}


